@import 'pallet';
@import 'util_mixins';

html,
body {
  min-width: 320px;
  font-size: 6.5px;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-kerning: auto;
  overflow-y: hidden !important;
  
  @media (min-width: $bp-xs-min) {
    overflow: hidden !important;
  }
  @media (min-width: $bp-s-min) {
    font-size: 7px;
  }
  @media (min-width: $bp-m-min) {
    font-size: 7.5px;
  }
  @media (min-width: $bp-l-min) {
    font-size: 8px;
  }
  @media (min-width: $bp-xl-min) {
    font-size: 8.5px;
  }
  @media (min-width: $bp-w-min) {
    font-size: 10px;
  }
  @media (min-width: $bp-fw-min) {
    font-size: 10.5px;
  }

  &:hover {
    .custom-cursor-back,
    .custom-cursor-info {
      opacity: 1;
    }
  }

  ::-moz-selection {
    color: $s-l;
    background: $p-m;
  }
  
  ::selection {
    color: $s-l;
    background: $p-m;
  }
}

html,
button,
a {
  cursor: none;
}

header {
  display: flex;
}

#root {
  // width: 100vw;
  // min-width: 320px;
  // height: 100vh;
  height: 100%;
  // overflow-y: auto;
  overflow: hidden;

  @media (min-width: 320px) {
    overflow-x: hidden;
  }
  &::-webkit-scrollbar {
    width: 0;
    opacity: 0;
  }
  &::-webkit-scrollbar-thumb {
    opacity: 0;
  }
  &::-webkit-scrollbar-track {
    opacity: 0;
  }
}

.smooth-scroll-frame {
  height: 100vh;

  .scroll-content {
    will-change: transform;
  }

  .scrollbar-track {
    // background-color: $bl;
    background-color: transparent;
    mix-blend-mode: difference;

    // &:before{
    //   content: '';
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   width: 100%;
    //   height: 100%;
    //   border-radius: inherit;
    //   pointer-events: none;
    //   -webkit-user-select: none;
    //   -moz-user-select: none;
    //   -ms-user-select: none;
    //   user-select: none;
    //   background-color: $wh;
    // }

    .scrollbar-thumb {
      mix-blend-mode: difference;
      background-color: $bl;

      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        pointer-events: none;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        background-color: white;
      }
    }
  }
  
}

